.person-frame {
    position: absolute;
    left: 40vw;
    bottom: 20vh;
}

.person-frame.girl {
    left: calc(40vw + 50px);
}

.person-frame>.head {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: antiquewhite;
    margin: 0 auto;
}

.head>.hair {
    width: 30px;
    height: 13px;
    background-color: black;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    -webkit-transform: rotate(-30deg);
        -ms-transform: rotate(-30deg);
            transform: rotate(-30deg);
    -webkit-transform-origin: 15px 15px;
        -ms-transform-origin: 15px 15px;
            transform-origin: 15px 15px;
}

.ponytail {
    background-color: black;
    width: 10px;
    height: 9px;
    border-radius: 100%;
    position: absolute;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    top: -3px;
    left: -2px;
}

.ponytail>.hanging {
    background-color: black;
    width: 5px;
    height: 40px;
    position: absolute;
    left: 8px;
    top: 2px;
    -webkit-transform: rotate(-35deg);
        -ms-transform: rotate(-35deg);
            transform: rotate(-35deg);
    border-top-left-radius: 100px;
    border-bottom-right-radius: 100px;
}

.person-frame>.body {
    width: 25px;
    height: 50px;
    background-color: pink;
    margin: 0 auto;
    border-radius: 6px;
}

.person-frame.jumping {
    -webkit-animation: jumping infinite 1s alternate ease-in;
            animation: jumping infinite 1s alternate ease-in;
}

@-webkit-keyframes jumping {
    from { -webkit-transform: translateY(0px); transform: translateY(0px); }
    to { -webkit-transform: translateY(-20px); transform: translateY(-20px) }
}

@keyframes jumping {
    from { -webkit-transform: translateY(0px); transform: translateY(0px); }
    to { -webkit-transform: translateY(-20px); transform: translateY(-20px) }
}

.person-frame.girl.jumping {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}

.girl>.body {
    height: 46px;
    width: 22px;
    background-color: peru;
}

@-webkit-keyframes girl-entering {
    from { -webkit-transform: rotateY(180deg) translateX(-50vw); transform: rotateY(180deg) translateX(-50vw) }
    to { -webkit-transform: rotateY(180deg) translateX(0vw); transform: rotateY(180deg) translateX(0vw) }
}

@keyframes girl-entering {
    from { -webkit-transform: rotateY(180deg) translateX(-50vw); transform: rotateY(180deg) translateX(-50vw) }
    to { -webkit-transform: rotateY(180deg) translateX(0vw); transform: rotateY(180deg) translateX(0vw) }
}

.girl.entering {
    -webkit-animation-name: girl-entering;
            animation-name: girl-entering;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    /* linear; */
}

.girl.facing {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}

.person-frame>.legs {
    height: 30px;
}

.legs>.leg {
    position: absolute;
    left: calc(50% - 7px);
    width: 15px;
    height: 30px;
    background-color: lightseagreen;
    border-radius: 6px;
    -webkit-animation: leg-rotate infinite 0.5s linear alternate;
            animation: leg-rotate infinite 0.5s linear alternate;
    -webkit-transform-origin: top;
        -ms-transform-origin: top;
            transform-origin: top;
}

.girl>.legs>.leg {
    background-color: darkolivegreen;
    width: 13px;
    left: calc(50% - 6px);
}

.leg.reverse,
.arm.reverse {
    -webkit-animation-direction: alternate-reverse;
            animation-direction: alternate-reverse;
}

@-webkit-keyframes leg-rotate {
    from { -webkit-transform: rotate(-15deg); transform: rotate(-15deg); }
    to { -webkit-transform: rotate(15deg); transform: rotate(15deg); }
  }

@keyframes leg-rotate {
    from { -webkit-transform: rotate(-15deg); transform: rotate(-15deg); }
    to { -webkit-transform: rotate(15deg); transform: rotate(15deg); }
  }

.person-frame>.arm {
    position: absolute;
    left: calc(50% - 6px);
    top: 35%;
    width: 12px;
    height: 32px;
    background-color: antiquewhite;
    border-radius: 6px;
    -webkit-animation: arm-rotate infinite 0.5s linear alternate;
            animation: arm-rotate infinite 0.5s linear alternate;
    -webkit-transform-origin: top;
        -ms-transform-origin: top;
            transform-origin: top;
}

.girl>.arm.holding {
    -webkit-animation-iteration-count: 0;
            animation-iteration-count: 0;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.boy>.arm.holding {
    -webkit-animation-iteration-count: 0;
            animation-iteration-count: 0;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

@-webkit-keyframes arm-rotate {
    from { -webkit-transform: rotate(30deg); transform: rotate(30deg); }
    to { -webkit-transform: rotate(-30deg); transform: rotate(-30deg); }
  }

@keyframes arm-rotate {
    from { -webkit-transform: rotate(30deg); transform: rotate(30deg); }
    to { -webkit-transform: rotate(-30deg); transform: rotate(-30deg); }
  }




